            if (cachedSchema) {
                return { schema: cachedSchema, url: schemaUri };
            }
            const response = await fetch(schemaUri, { signal });
            const schema = await response.json();
            this.schemaCache.set(cacheKey, schema);
            return { url: schemaUri, schema };
