import { parseAllDocuments, parseDocument } from 'yaml';
export function parseYamlDocument(text, lineCounter) {
    return parseDocument(text, { lineCounter, uniqueKeys: false, strict: false, logLevel: 'silent' });
}
/**
 * Wrapper that ensures consistent options
 */
export function parseAllYamlDocuments(text, lineCounter) {
    return parseAllDocuments(text, {
        lineCounter,
        uniqueKeys: false,
        strict: false,
        logLevel: 'silent',
    });
}
